.shipping-section{
    background: #FFFFFF 0% 0% no-repeat padding-box;
border: 1px solid #D1D3D6;
border-radius: 2px;

    ul{
        list-style: none;
        margin: 0;
        margin-left: -36px;
        li{
            height: 49px;
            display: flex;
            justify-content: space-between;
            .left-section{
                display: flex;
                justify-content: space-between;
                width: 90%;
                align-self: center;

                .title{
                    text-align: left;
                    font: normal normal normal 20px/25px Muli;
                    letter-spacing: 0px;
                    color: #172026;
                }
                .sub-title{
                    text-align: right;
                    font: normal normal normal 14px/21px Muli;
                    letter-spacing: 0px;
                    color: #172026;
                }
            }

            .right-section{
                width: 10%;
                align-self: center;
                text-align: -webkit-center;

            }
        }
    }
}